
#state-code{text-align:center;height:100%;background-color:#000;overflow:auto
}
#state-code .phi-input{display:block;color:#fff
}
#state-code form{width:270px;margin:auto;margin-top:76px;background:url("../assets/phidias-label.png") no-repeat center top;padding-top:112px
}
#state-code form footer{margin:48px 0
}
#state-code #toggle-dialog{display:block;color:#fff;opacity:0.8;cursor:pointer;padding:8px 12px;padding-top:32px
}
#dialog .dialog-container{text-align:left;padding:12px 24px
}
#dialog .dialog-container h2{margin:0;margin-bottom:1em
}
#dialog .dialog-container p{margin:0
}
#dialog .dialog-container strong{font-weight:bold
}
#dialog .dialog-container .buttons{margin-top:24px;text-align:right
}



