
.slidepost-enter-active,.slidepost-leave-active{position:absolute;top:8px;left:8px;right:8px;transition:transform .32s,opacity .32s
}
.slidepost-enter-active footer,.slidepost-leave-active footer{display:none
}
.slidepost-enter,.slidepost-leave-active{opacity:0
}
.moving-left .slidepost-enter{transform:translate3d(0, 64px, 0)
}
.moving-left .slidepost-leave-active{transform:translate3d(0, -64px, 0)
}
.moving-right .slidepost-enter{transform:translate3d(0, -64px, 0)
}
.moving-right .slidepost-leave-active{transform:translate3d(0, 64px, 0)
}



