
.slidetab-enter-active,.slidetab-leave-active{position:absolute;top:8px;left:8px;right:8px;transition:transform .32s,opacity .32s
}
.slidetab-enter-active ons-fab,.slidetab-leave-active ons-fab{display:none
}
.slidetab-enter,.slidetab-leave-active{opacity:0
}
.moving-left .slidetab-enter{transform:translate3d(120px, 0, 0)
}
.moving-left .slidetab-leave-active{transform:translate3d(-120px, 0, 0)
}
.moving-right .slidetab-enter{transform:translate3d(-120px, 0, 0)
}
.moving-right .slidetab-leave-active{transform:translate3d(120px, 0, 0)
}



